export default {
	name: 'clickCircle',
	data() {
		return {
			isReady:1,
			isCricle:'',
			circleShow:0,
			ainimaChoose:''
		};
	},
	created() {
		if(Math.random() >= 0.5) {
			this.ainimaChoose = 'animation0'
		} else{
			this.ainimaChoose = 'animation1'
		}
	},
	methods:{
		conClick(e){
			var isReady = this.isReady
			if (isReady === 1) {
			  var isTop = Math.round(e.changedTouches[0].clientY) - 25 + 'px'
			  var isLeft = Math.round(e.changedTouches[0].clientX) - 25 + 'px'
				this.isCricle = `top:${isTop};left:${isLeft};`
				this.circleShow = 1
				this.isReady = 0
				var that = this
			  setTimeout(() => {
				  that.circleShow = 0
				  that.isReady = 1
			  }, 300)
			}
		}
	}
}
